import Component from './Component';

export default class ListItem extends Component {

    constructor(value) {
        super();
    }

	render() {
		return this.theme(this.constructor.name);
	}

	/*
	 * Output the object instance as a string
	 *
	 * @return string
	*/
	toString() {
		return this.$el.outerHTML;
	}

}
